import React from "react";
import { NavLink } from "react-router-dom";

const Common = (props) => {
  return (
    <>
      <section className="header d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row">
                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                  <h1>
                    {props.name}
                    <strong className="brand_name"> JaymanPatel</strong>
                  </h1>
                  <h2 className="my-3">{props.para}</h2>
                  <div className="mt-3">
                    <NavLink className="btn_get_started" to={props.visit}>
                      {props.btnname}
                    </NavLink>
                  </div>
                </div>
                <div className="col-md-6 order-1 order-lg-2 header_img">
                  <img
                    src={props.imgSrc}
                    className="img-fluid animated_img"
                    alt="Error"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Common;
